import React,{Component} from 'react'
import {Input, Button,message,Image,Modal} from 'antd';
import 'antd/dist/antd.css'; // or 'antd/dist/antd.less'
import './App.css'
import {ErrorMsg} from "./notifications";
import FieldNumberOutlined from "@ant-design/icons/lib/icons/FieldNumberOutlined";
import RobotOutlined from "@ant-design/icons/lib/icons/RobotOutlined";
import {UserOutlined} from "@ant-design/icons";
export default class CtfForm extends Component {
    constructor() {
        super();
        this.state = {
            cptInput:'',
            cptShow:'none',
            submitLoading:false,
            flag:'',
            cptUrl:'',
            qrVisible:false,
        }
    }

    componentWillMount = ()=> {
        fetch("/web5/captcha").then(res=>res.text()).then(res=>{
            this.setState({cptUrl:res})
        }).catch(e=>{
            console.log(e)
            ErrorMsg("网络错误：")
        })
    }
    handleCaptchaClick = ()=>{
        this.setState({cptShow:'block'})
    }
    handleChange = (e)=>{
        this.setState({cptInput:e.target.value})
    }
    handleSubmit = ()=>{
        if(this.state.cptInput===''){
            ErrorMsg("验证码不可为空！")
            return
        }
        this.setState({'submitLoading':true})
        fetch("/web5/captcha",{
            body:JSON.stringify({
                'captcha':this.state.cptInput
            }),
            method:'POST'
        }).then(response=>response.json()).then(
            res=>{
                this.setState({'submitLoading':false})
                if(res.success===true){
                    message.success(res.msg,10)
                    this.setState({flag:res.msg})
                } else {
                    ErrorMsg(res.msg)
                    console.log("url updated")
                    this.setState({cptInput:'',cptUrl:res.cpt})
                }
            }
        ).catch((e)=>{
            console.log(e);
            ErrorMsg("网络错误：")
            this.setState({'submitLoading':false})
        })
    }
    info = ()=> {
        Modal.info({
            title: '支付宝扫码转账也无法在这里获得flag',
            content: (
                <div>
                    <Image
                        src={"./qr.png"}
                    />
                </div>
            ),
            onOk() {},
        });
    }

    render(){
        const {cptShow,flag,cptInput,cptUrl} = this.state;
        return (
            <div>

                <form className="form-horizontal">
                    <div className="form-group">
                        <Input
                            prefix={<UserOutlined/>}
                            placeholder="你的姓名"
                            name="username"
                            /*onChange={this.handleChange}*/
                            allowClear
                            size="large"
                        />
                    </div>
                    <div className="form-group form-input">
                        <Input
                            allowClear
                            required
                            name="number"
                            placeholder="你的学号"
                            prefix={<FieldNumberOutlined />}
                            /*onChange={this.handleChange}*/
                            size="large"
                        />
                    </div>
                    <div className="form-group form-input">
                        <Input
                            allowClear
                            required
                            name="number"
                            placeholder="请填写验证码"
                            prefix={<RobotOutlined />}
                            onChange={this.handleChange}
                            size="large"
                            onMouseOver={this.handleCaptchaClick}
                            value={cptInput}
                        />
                    </div>
                    <div className="form-group form-input" style={{display:cptShow}}>
                        <Image
                            width={200}
                            height={50}
                            src={cptUrl}
                            /*fallback="data:image/png;base64,"
*/
                        />
                    </div>
                    <div className="form-group form-input">
                        <Button
                            type="primary"
                            onClick={this.handleSubmit}
                            loading={this.state.submitLoading}
                        >0元购!
                        </Button>
                        <span style={{marginLeft:10,marginRight:10}}>或</span>
                        <Button
                            type="primary"
                            onClick={this.info}
                        >10元购
                        </Button>
                    </div>
                    <span className={'form-input'}>{flag}</span>
                </form>
            </div>
        )
    }
}